import { useState } from "react";
import TextField from "@material-ui/core/TextField";
import { Chip } from "@material-ui/core";

function App() {
  const [chips, setChips] = useState<string[]>([]);

  const handleInput = ({ target }: any) => {
    if (target.value[target.value.length - 1] === ",") {
      if (target.value.split(",")[0] === "") {
        target.value = "";
        return;
      }
      setChips([...chips, target.value.split(",")[0]]);
      target.value = "";
    }
  };

  const handleKeyDown = ({ key, target }: any) => {
    if(key === 'Backspace' && target.value.length > 0) return;

    if (key === "Backspace") {
      const removeChip = chips.filter(
        (chip, _, chips) => chip !== chips[chips.length - 1]
      );
      setChips(removeChip);
    }
  };

  return (
    <div style={{ display: "flex", gap: 5 }}>
      {chips.map((value, index) => (
        <Chip label={value} key={index} />
      ))}
      <TextField onInput={handleInput} onKeyDown={handleKeyDown} />
    </div>
  );
}

export default App;